				"html": "<p>几天前，看到了鬼脚七发的朋友圈：</p>\n<blockquote>\n<p>老老实实的丑，</p>\n</blockquote>\n<blockquote>\n<p>胜过装腔作势的美。</p>\n</blockquote>\n<blockquote>\n<p>现实的困境是，</p>\n</blockquote>\n<blockquote>\n<p>想美没资本，</p>\n</blockquote>\n<blockquote>\n<p>想丑没胆量</p>\n</blockquote>\n<p>当时只是觉得有点意思，没有深究。今天吃饭时，在想自己的博客为什么写的不顺畅，而且写出来有点假假的感觉。突然想到上面的一段话。然后有些明白:原来我以前写的文章，有种装腔作势的感觉。汗，难怪写的那么累，而且写出来觉得不自然。</p>\n<p>想写出高大上的文章没有错，但是水平不到，境界不到，最好不勉强。与其跳着、够着去争取一些自己能力之外的目标，不如先接受现实，写写自己的真实生活。</p>\n<p>真实的生活肯定是有很多不如人意；自己也肯定有很多缺点，会遇到很多窘境。虽然丑陋，但总算真实。请给我勇气吧，让我勇敢的拥抱丑陋。</p>",
